}
.inputs-wrap__label {
    display: flex;
    align-items: center;
    justify-content: space-between;
    font-size: 16px;
    transform: translateY(25px);
    color: #F3E7D7;
    transition: all 0.5s;
}

.new-order__add-form{
    padding: 15px;
    margin: 0 auto;
    width: 60%;
}

.inputs-wrap__text-inputs{
    position: relative;
    background: transparent;
    color: #F3E7D7;
    width: 100%;
    border: none;
    outline: none;
    padding: 8px 0;
    font-size: 16px;
}

.inputs-wrap {
    width: 100%;
    padding: 30px 0 0 0;
    position: relative;
}

.inputs-wrap:after, .inputs-wrap:before {
    content: "";
    position: absolute;
    display: block;
    width: 100%;
    height: 2px;
    background-color: #F3E7D7;
    bottom: 0;
    left: 0;
    transition: all 0.5s;
}

.inputs-wrap::after {
    background-color: #F3E7D7;
    transform: scaleX(0);
}

.is-active::after {
    transform: scaleX(1);
}

.is-active .inputs-wrap__label, .is-active .panel-icon {
    opacity: .6;
}

.is-completed .inputs-wrap__label {
    font-size: 12px;
    transform: translateY(0);
}
.btn-submit{
    display: flex;
    align-items: center;
    justify-content: space-between;
    background-color: #4A2849;
    font-size: 18px;
    padding: 10px 15px;
    margin: 15px auto;
    max-width: 50%;
